import { FormControl, InputLabel, makeStyles, Select } from "@material-ui/core";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { requestTheme } from "../redux/actions/actions";

const useStyles = makeStyles((theme) => ({
  formControl: {
    minWidth: 200,
  },
}));
const ThemeChanger = () => {
  const classes = useStyles();
  const [theme, setTheme] = useState('light')
  const dispatch = useDispatch()
  const handleChange = (event) => {
    setTheme(event.target.value)
    dispatch(requestTheme(event.target.value))
  };
  return (
    <>
      <FormControl className={classes.formControl}>
        <InputLabel htmlFor="outlined-age-native-simple" className="text-dark">Change Theme</InputLabel>
        <Select
          native
          value={theme}
          onChange={handleChange}
          id = "outlined-age-native-simple"
        >
          <option value={"light"}>Light</option>
          <option value={"beige"}>Beige</option>
          <option value={"dark"}>Dark</option>
          <option value={"forest"}>Forest</option>
          <option value={"petunia"}>Petunia</option>
          <option value={"purple"}>Purple</option>
          <option value={"redish"}>Redish</option>
        </Select>
      </FormControl>
    </>
  );
};
export default ThemeChanger;
